@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Titillium Web', sans-serif;
}
.home, .projects {
    display: flex;
    height: 60vh;
    flex-direction: column;
    align-items: center;
    font-size: 2rem;
}

/*About style*/
.about {
    display: flex;
    height: 60vh;
    flex-direction: column;
    align-items: center;
    font-size: 2rem;
}
    p{
        font-size: medium;
        margin: 0 5rem;
    }
/*Home style*/

.profile-img {
    border-radius: 50%;
    height: 50vh;
    border: #90ac58 1px;
    margin-top: 50px;

 }
.link-icons {
    text-decoration: none;
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 3px;
    cursor: pointer;
}


.navbar {
    background-color: #282c34;
    height:80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}
.nav-menu{
    background-color: #282c34;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
}
.nav-menu.active{
    left: 0;
    transition: 350ms;
}
.nav-text{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px 0 8px 16px;
    list-style: none;
    height: 60px;
}
.nav-text a{
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}
.nav-text a:hover{
    background-color: gold;
}
.nav-menu-items {
    width:100%;
    color: #f5f5f5;
}
.navbar-toggle{
    background-color: #282c34;
    width: 100%;
    height: 20%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
span {
    margin-left: 10px;
}
